nav {
	background: #fafafa;
	visibility: hidden;
	opacity: 0;
	z-index: 2;
}

.nav {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 100%;

	&__background {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}